.sectionheader {
    height: 96%;
    width: 96%;
    position: absolute;
    margin-top: 2%;
    margin-left: 2%;
    margin-right: 2%;
    font-weight: 600;

    & span {
        justify-content: center;
        align-items: start;
        padding-top: 5%;
        font-size: 4vw; }
    @include fontstart; }

.baivocontainer {
    height: 30%;
    position: absolute;
    width: 100%; }

.baivologo {
    width: 100%;
    height: auto;
    display: inline-block;
    margin-bottom: 1%; }
.baivo {
    height: 9vw;
    width: auto; }
.baivolautschrift {
    width: 25vw;
    height: 10vw; }
.lautschrift {
    height: 3vw; }
.sozialnav {
    position: absolute;
    height: 96%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    color: white;

    & ul {
        height: 40%;
        width: 2rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-right: 2%;
 } }        // background-color: blue

.sozialborder {

    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    margin-left: 0;
    margin-right: 0;
    // background-color: grey
    border-right: 2px solid black; }

.sozialicon {
    height: 1.5vw;
    width: 1.5vw;
    text-decoration: none;
    background-repeat: no-repeat;
    background-position: center; }

.video-container {
    height: 100%;
    width: 100%;
    position: absolute;
    display: flex;
    justify-content: center; }

.video {
    width: 90vw;
    height: auto; }


@media screen and ( max-width: 425px ) {
    .sectionheader {
        height: 50%; }
    .sozialnav {
        & ul {
            height: 30%;
            width: 7%; } }

    .sozialicon {
        height: 4vw;
        width: 4vw; }

    .video-container {
        height: 50%; } }



