@mixin backgroundimagecentercover {
    background-size: cover;
    background-repeat: none;
    background-position: center; }

@mixin fontstart {
    font-family: 'Raleway', sans-serif;
    text-transform: uppercase; }

@mixin aligncenter {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-end; }
